import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'
import 'html5-reset/assets/css/reset.css'

import testProductDetail from '@/view/crowdTest/test-product-detail.vue'; //众测产品详情页

import moment from 'moment'
Vue.filter('dateFormat', function (value, type) { //时间格式化过滤
  return moment(value).format(type);
});


Vue.use(Router)

const router = new Router({
  // mode: 'history',
  // base: '/test-product-detail.html',
  routes: [{
    path: '/',
    name: 'testProductDetail',
    component: testProductDetail
  }]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



